import { FC } from 'react';

import {
  StyledWrapper,
  StyledList,
  StyledItem,
  StyledIconContainer,
  StyledIcon,
  StyledAction,
  StyledPrice,
  StyledTitle,
  StyledUpdated,
} from './PriceList.styles';
import { PriceListProps } from './PriceList.types';

const PriceItem: FC<{ price: string }> = ({ price }) => (
  <StyledPrice>{price}</StyledPrice>
);

const Dash: FC = () => <strong>&mdash;</strong>;

const PriceList: FC<PriceListProps> = ({ title, updated, items }) => {
  if (!Array.isArray(items) || !items?.length) {
    return null;
  }

  return (
    <StyledWrapper>
      {title && <StyledTitle>{title}</StyledTitle>}
      {updated && <StyledUpdated>Prices last updated: {updated}</StyledUpdated>}
      <StyledList>
        {items.map(({ action, price, icon }, i) => {
          return (
            <StyledItem key={i}>
              <StyledIconContainer>
                <StyledIcon svg={icon} />
              </StyledIconContainer>
              <StyledAction>{action}</StyledAction>
              {price ? <PriceItem price={price} /> : <Dash />}
            </StyledItem>
          );
        })}
      </StyledList>
    </StyledWrapper>
  );
};

export default PriceList;
